import React, { Component } from 'react';
import './Header.module.css'
import { Container, Col, Media, Button } from 'react-bootstrap';
import black from '../black.jpg'
import blackbot from '../blackbot.jpg'
import bluebot from '../bluebot.jpg'
import bluetop from '../bluetop.jpg'
import redbot from '../redbot.jpeg'
import redtop from '../redtop.jpg'
import whitebot from '../whitebot.jpg'
import monoblacktop from '../monoblacktop.jpg'
import monoblackbot from '../monoblackbot.jpg'
import Slider from './Slider'


const Home = () => {
        return (
            <div><Slider />
            <Container style={{width:'650px'}}>
                      
                <Col md='9'>
                    <Media className='m-5' variant='central' >
                        <img width='550' height='400' className='mr-3' src={black}/>
                        <Media.Body>
                            <h5>2999р</h5>
                            <p>Высокие черные</p>
                        </Media.Body>
                    </Media>
                    <Media className='m-5' >
                        <img width='550'height='400' className='mr-3' src={blackbot}/>
                        <Media.Body>
                            <h5>2699р</h5>
                            <p>Черные низкие</p>
                        </Media.Body>
                    </Media>
                    <Media className='m-5' >
                        <img width='550' height='400' className='mr-3' src={bluebot}/>
                        <Media.Body>
                            <h5>2999р</h5>
                            <p>Синие низкие</p>
                        </Media.Body>
                    </Media>
                    <Media className='m-5' variant='central' >
                        <img width='500' height='400' className='mr-3' src={bluetop}/>
                        <Media.Body>
                            <h5>2999р</h5>
                            <p>Синие высокие</p>
                        </Media.Body>
                    </Media>
                    <Media className='m-5' variant='central' >
                        <img width='450' height='400' className='mr-3' src={redbot}/>
                        <Media.Body>
                            <h5>2699р</h5>
                            <p>Красные низкие</p>
                        </Media.Body>
                    </Media>
                    <Media className='m-5' variant='central' >
                        <img width='450' height='400' className='mr-3' src={redtop}/>
                        <Media.Body>
                            <h5>2999р</h5>
                            <p>Красные высокие</p>
                        </Media.Body>
                    </Media>
                    <Media className='m-5' >
                        <img width='550'height='400' className='mr-3' src={whitebot}/>
                        <Media.Body>
                            <h5>2699р</h5>
                            <p>Белые низкие</p>
                        </Media.Body>
                    </Media>
                    <Media className='m-5' variant='central' >
                        <img width='550' height='400' className='mr-3' src={monoblacktop}/>
                        <Media.Body>
                            <h5>2999р</h5>
                            <p>Высокие полностью черные</p>
                        </Media.Body>
                    </Media>
                    <Media className='m-5' variant='central' >
                        <img width='550' height='400' className='mr-3' src={monoblackbot}/>
                        <Media.Body>
                            <h5>1699р</h5>
                            <p>Низкие полностью черные</p>
                        </Media.Body>
                    </Media>
                </Col>
            </Container>
            </div>
        )

}

export default Home