import React from 'react'
import monoblackbot from '../monoblackbot.jpg'
import { Carousel } from 'react-bootstrap'
import converse1 from '../converse1.jpg'
import converse2 from '../converse2.jpg'
import converse3 from '../converse3.jpg'

const Slider = () => {
     return (
        <Carousel>
            <Carousel.Item>
                <img
                    className='d-block w-100'
                    src={converse1}
                />
                <Carousel.Caption>
                    <h1 bg='dark'>
                        Вечная классика
                    </h1>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
                <img
                    className='d-block w-100'
                    src= {converse2}
                />
                <Carousel.Caption>
                    <h1>
                        Культовые кеды
                    </h1>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
                <img
                    className='d-block w-100'
                    src={converse3}
                />
                <Carousel.Caption>
                    <h1>
                        Миллионы фанатов по всему миру
                    </h1>
                </Carousel.Caption>
            </Carousel.Item>
        </Carousel>
    )
}

export default Slider