import React, { Component } from 'react';
import { Container, Form , Button} from 'react-bootstrap';

const Order = () => {
        return (
              <Container style={{width: '450px'}}>
                  <h1>
                      Позвоните по номеру 777777 или закажите обратный звонок
                  </h1>
                  <Form>
                      <Form.Group controlId='formBasicNumber'>
                        <Form.Label>
                            Номер телефона
                        </Form.Label>
                        <Form.Control type='phone' placeholder='Введите номер' required/>
                      </Form.Group>
                      <Button variant='primary' type='submit'>
                            Отправить
                      </Button>
                  </Form>
              </Container>
        )
}

export default Order